var graceChecker = require("@/common/graceChecker.js");
	import pwInput from '@/components/input/PwInput'
export default {
	components: {
		pwInput
	},
	data() {
		return {
			userName: '',
			code: '',
			passWord: '',
			passWorda: '',
			second: 90,
			showText: true
		};
	},
	onLoad() {

	},
	methods: {
		sendCode() { //发送验证码
			var _this = this;
			if (_this.userName == "") {
				_this.$tools.toast('请填写正确的手机号码')
				return false;
			}

			//发送验证码
			_this.$request.common
				.sendCode({
					phone: _this.userName,
					type: 1
				})
				.then(data => {
					_this.$tools.loadingHide();
					if (data.status == 200) {

						_this.showText = false
						var interval = setInterval(() => {
							let times = --_this.second
							_this.second = times < 10 ? '0' + times : times //小于10秒补 0
						}, 1000)
						setTimeout(() => {
							clearInterval(interval)
							_this.second = 90
							_this.showText = true
						}, 90000)

					} else {
						_this.$tools.toast(data.msg)
					}
				})
				.catch(err => {
					_this.$tools.loadingHide();
					//消息异常
					_this.$tools.toast('数据加载异常')
				});

		},
		btnClick(){
			var _this = this;
			//定义表单规则
			var rule = [{
					name: "userName",
					checkType: "phoneno",
					errorMsg: "电话号码不正确"
				},
				{
					name: "code",
					checkType: "string",
					checkRule: "6,6",
					errorMsg: "验证码6个字符"
				},
				{
					name: "passWord",
					checkType: "string",
					checkRule: "6,20",
					errorMsg: "密码为6-20个字符"
				}
			];
			//进行表单检查
			var formData = {
				userName : this.userName,
				code : this.code,
				passWord : this.$refs['passWord'].getPassWord()
			}
			
			var checkRes = graceChecker.check(formData, rule)
			if (checkRes) {
				_this.$tools.loading('数据提交中')
				_this.$request.user
					.changePwd(formData)
					.then(data => {
						_this.$tools.loadingHide();
						if (data.status == 200 || data.status == 201) {
							uni.navigateTo({
								url: '/pages/member/login'
							});
						} else {
							_this.$tools.toast(data.msg)
						}
					})
					.catch(err => {
						_this.$tools.loadingHide();
						//消息异常
						_this.$tools.toast('数据加载异常')
					});
			} else {
				_this.$tools.toast(graceChecker.error)
			}
		},
		formSubmit(e) {
			var _this = this;
			//定义表单规则
			var rule = [{
					name: "userName",
					checkType: "phoneno",
					errorMsg: "电话号码不正确"
				},
				{
					name: "code",
					checkType: "string",
					checkRule: "6,6",
					errorMsg: "验证码6个字符"
				},
				{
					name: "passWord",
					checkType: "string",
					checkRule: "6,20",
					errorMsg: "密码为6-20个字符"
				}
			];
			//进行表单检查
			var formData = e.detail.value;

			var checkRes = graceChecker.check(formData, rule)
			if (checkRes) {
				_this.$tools.loading('数据提交中')
				_this.$request.user
					.changePwd(formData)
					.then(data => {
						_this.$tools.loadingHide();
						if (data.status == 200 || data.status == 201) {
							uni.navigateTo({
								url: '/pages/member/login'
							});
						} else {
							_this.$tools.toast(data.msg)
						}
					})
					.catch(err => {
						_this.$tools.loadingHide();
						//消息异常
						_this.$tools.toast('数据加载异常')
					});
			} else {
				_this.$tools.toast(graceChecker.error)
			}
		}
	}
};
